<script>
import WOW from "wowjs";
export default {
  name: "HomeView",
  data() {
    return {
      logo: true,
      menu: true,
    };
  },
  methods: {
    onScroll() {
      this.$refs.navbar.classList.toggle("fixed-nav-bar", window.scrollY > 400);
      if (window.scrollY >= 800) {
        this.logo = false;
      } else {
        this.logo = true;
      }
      const sections = document.querySelectorAll("section");
      const sectionsPos = {};
      sections.forEach((section) => {
        sectionsPos[section.id] = section.offsetTop;
      });
      var scrollPos =
        document.documentElement.scrollTop || document.body.scrollTop;
      for (var id in sectionsPos) {
        if (sectionsPos[id] <= scrollPos + 100) {
          document.querySelector(".active").classList.remove("active");
          document
            .querySelector(`.nav-links li a[href*=${id}]`)
            .classList.add("active");
        }
      }
    },
    openNav() {
      this.menu = false;
      this.$refs.menu.classList.add("active");
      // this.$refs.bar.classList.value = "fa-solid fa-xmark fs-5";
    },
    close() {
      this.menu = true;
      this.$refs.menu.classList.remove("active");
    },
  },
  mounted() {
    window.addEventListener("scroll", this.onScroll);
    let wow = new WOW.WOW({
      boxClass: "wow",
      animateClass: "animated",
      offset: 0,
      mobile: true,
      live: true,
    });
    wow.init();
  },
};
</script>
